var HTTPUtil = {};

HTTPUtil.get = function(url) {
  return fetch(url, {
    method: "GET",
    mode: "cors"
  }).then(function(response) {
    return response.json();
  });
};

HTTPUtil.post = function(url, formData) {
  return fetch(url, {
    method: "POST",
    mode: "cors",
    credentials: "include",
    headers: { Accept: "application/json", "Content-Type": "application/json" },
    body: formData
  }).then(response => {
    return response.json();
  });
};

export default HTTPUtil;
