import React from "react";
import { withStyles, MuiThemeProvider } from "@material-ui/core/styles";
import compose from "recompose/compose";

import Card from "@material-ui/core/Card";

import TextField from "@material-ui/core/TextField";

import Button from "@material-ui/core/Button";

import Typography from "@material-ui/core/Typography";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Slide from "@material-ui/core/Slide";

import Theme from "../Theme";

import Hu from "../HttpUtil";
import copy from "copy-to-clipboard";
var QRCode = require("qrcode.react");

const styles = theme => ({
  root: {
    display: "flex",
    flexDirection: "column",
    background: "#fff",
    maxWidth: "100%",
    height: "100%",
    position: "relative",
    zIndex: 2,
    alignItems: "center",
    justifyContent: "center"
  },
  card: {
    width: 450,
    minHeight: 480,
    paddingLeft: 40,
    paddingRight: 40,
    flexDirection: "column",
    alignItems: "center"
  },
  div0: {
    position: "absolute",
    height: window.innerHeight - 64,
    marginTop: "64px",
    width: "100%"
  },
  textField: {
    width: "100%",
    marginTop: "20px",
    height: "78px",
    paddingBottom: "16px",
    position: "relative",
    verticalAlign: "top"
  },
  i: {
    fontSize: "12px",
    marginTop: "-32px",
    userSelect: "none"
  }
});
function Transition(props) {
  return <Slide direction="up" {...props} />;
}

class MainCard extends React.Component {
  state = {
    ip: "http://192.168.1.1",
    open: false,
    bs: "By zsakvo"
  };

  handleResize = () => {
    if (window.innerWidth <= 600) {
      this.setState({
        rootCardWidth: window.innerWidth - 80,
        rootCardHeight: window.innerHeight,
        eWidth: "100%"
      });
    } else {
      this.setState({
        rootCardWidth: "auto",
        rootCardHeight: "auto",
        eWidth: "350px"
      });
    }
  };

  componentDidMount() {
    if (window.innerWidth <= 600) {
      this.setState({
        rootCardWidth: window.innerWidth - 80,
        rootCardHeight: window.innerHeight,
        eWidth: "100%"
      });
    } else {
      this.setState({
        rootCardWidth: "auto",
        rootCardHeight: "auto",
        eWidth: "350px"
      });
    }
    window.addEventListener("resize", this.handleResize);
  }

  gbs = async () => {
    var bsn = this.bookSourceName.value;
    var bsu = this.bookSourceUrl.value;
    var body = JSON.stringify({
      bsn: bsn,
      bsu: bsu
    });
    var bs = await Hu.post("/gbs", body);
    this.setState({
      open: true,
      bs: JSON.stringify(bs)
    });
  };

  onCopy = () => {
    copy(this.state.bs);
    this.handleClose();
  };
  handleClickOpen = () => {
    this.setState({ open: true });
  };

  handleClose = () => {
    this.setState({ open: false });
  };

  render() {
    const { classes } = this.props;
    return (
      <MuiThemeProvider theme={Theme}>
        <Dialog
          open={this.state.open}
          TransitionComponent={Transition}
          keepMounted
          onClose={this.handleClose}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"
        >
          <DialogTitle id="alert-dialog-slide-title">{"导入书源"}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-slide-description">
              你可以用手机直接扫描二维码获取书源代码，亦可以直接点击按钮直接复制代码到你的设备。
              <br />
              <br />
              <QRCode value={this.state.bs} size={260} />
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.onCopy} color="primary">
              复制书源内容
            </Button>
          </DialogActions>
        </Dialog>
        <div className={classes.div0}>
          <div className={classes.root}>
            <Card
              className={classes.card}
              style={{
                display: "flex",
                width: this.state.rootCardWidth,
                height: this.state.rootCardHeight
              }}
            >
              <div>
                <Typography
                  variant="h6"
                  color="inherit"
                  style={{ marginTop: "64px", marginBottom: "12px" }}
                >
                  请自定义参数
                </Typography>
                <TextField
                  InputLabelProps={{ shrink: true }}
                  defaultValue="追书-My716"
                  label={"bookSourceName"}
                  className={classes.textField}
                  inputRef={el => (this.bookSourceName = el)}
                />
                <div className={classes.i}>自定义书源名称</div>
                <TextField
                  InputLabelProps={{ shrink: true }}
                  defaultValue={this.state.ip}
                  label={"bookSourceUrl"}
                  className={classes.textField}
                  inputRef={el => (this.bookSourceUrl = el)}
                />

                <div className={classes.i}>
                  输入本机的外网ip/域名（需要以http://开头，若为ip则确保本程序运行于80端口）
                </div>
                <Button
                  variant="contained"
                  color="primary"
                  style={{ marginTop: "64px" }}
                  onClick={() => this.gbs()}
                >
                  {"生成书源"}
                </Button>
              </div>
            </Card>
          </div>
        </div>
      </MuiThemeProvider>
    );
  }
}
export default compose(withStyles(styles))(MainCard);
